export default {
	data() {
		return {
			tboxList:[]
		}
	},
	onLoad() {
		// 获取购物车数据
		this.tboxList = uni.getStorageSync("tbox_audio");
	},
	methods: {
		// 商品详情
		go_shopDetails(idx){
			uni.navigateTo({
				url:"/pages/shopDetails/shopDetails?id="+idx
			})
		},
		// 音乐
		goMic(idx) {
			uni.navigateTo({
				url: "/pages/micPlayPage/micPlayPage?id=" + idx
			})
		},
	}
}
